import "./assets/css/style.css";
import "./assets/css/main.css";
import "./App.css";
import { BsFillAwardFill } from "react-icons/bs";
import Logo from "./assets/img/img.png";
import {useConf,useGetUsername, useSmartMenu } from "./services/api";
import {useEffect} from "react";
import Cookies from "js-cookie";
import Loader from "./assets/img/loader.gif"

function App() {

  const { data: myConf, isLoading: confLoading } = useConf();
  const { data: myName, isLoading: userLoading } = useGetUsername(myConf, Cookies.get("portal_msvstudio"));
  const { data: myMenu, isLoading: smartLoading } = useSmartMenu(myConf, myName?.username);
  

  const checkCookie = () => {
    let cookies = Cookies.get("portal_msvstudio");
    if (typeof cookies === "undefined") {
      window.location.href = "https://dev.msvstudio.co.id/auth";
    } else {
    }
  };

  useEffect(() => {
    checkCookie();
  }, []);

  return (
    <div className="App">
      <>
        <section className="video-bg">
          <div className="container">
            {/* <video
              loop={true}
              autoPlay={true}
              src="https://r5---sn-npoe7n7y.googlevideo.com/videoplayback?expire=1623928204&ei=LNnKYLOBGKKy2_gP96OAwAc&ip=173.213.87.45&id=o-AG54lr1ZNwLH3CcT-nAiwSqtdnmVG_sx5xZSz7Y7piSd&itag=137&aitags=133%2C134%2C135%2C136%2C137%2C160%2C242%2C243%2C244%2C247%2C248%2C278&source=youtube&requiressl=yes&vprv=1&mime=video%2Fmp4&ns=qJPZMxkOryRt0XdEBh6bRYEF&gir=yes&clen=17719786&otfp=1&dur=60.874&lmt=1616729791725778&keepalive=yes&fexp=24001373,24007246&c=WEB&txp=6216222&n=sWLtAtJlR4uAf5an6&sparams=expire%2Cei%2Cip%2Cid%2Caitags%2Csource%2Crequiressl%2Cvprv%2Cmime%2Cns%2Cgir%2Cclen%2Cotfp%2Cdur%2Clmt&sig=AOq0QJ8wRQIhANUWCtlBsr6wBeWGUti7xVUw10D2arCf5sqU0n-ooM5JAiBzBjM5KET38zICT4YHiEOUjJKtKuyxdMrHwoUdY1jTnw%3D%3D&rm=sn-42a4pcxgoxu-hp5e7e&req_id=4cbf0056b497a3ee&redirect_counter=2&cm2rm=sn-q4felr76&cms_redirect=yes&mh=XN&mip=180.214.243.210&mm=34&mn=sn-npoe7n7y&ms=ltu&mt=1623906079&mv=u&mvi=5&pl=24&lsparams=mh,mip,mm,mn,ms,mv,mvi,pl&lsig=AG3C_xAwRQIgXBHOZP0Lywb3echAuvuw27jsd-Y77T0VyDvlkeu_DrwCIQCfzJcrpdGKxsoa0U2iUt4JLT0SPlsYhdpIoo7duo6Uqg%3D%3D"
            /> */}
          </div>
        </section>
        {!userLoading && !smartLoading ? 
        <>
        <section className="header">
          <div className="container pt-5">
            <div className="row">
              <div className="col text-center">
                <img
                  src={Logo}
                  alt=""
                  className="App-logo img-fluid"
                  style={{ maxWidth: "125px" }}
                />
              </div>
            </div>
            <div className="title mt-5">
              <h1 className="fw-bold">
                Selamat Datang {myName?.username}, di MSV Portal
              </h1>
            </div>
            <div className="logout my-5">
              <a href="http://dev.msvstudio.co.id/auth/logout"><button className="btn btn-danger rounded-pill px-3 py-2">Logout</button></a>
              </div>
          </div>
        </section>
        <section className="main mt-5">
          <div className="container">

              <div className="main-wrapper">
                {myMenu?.map((data) => (
                  <a href={data.url}>
                    <div className={`badge ${data.color}`}>
                      <h1 className="circle text-dark">
                        <BsFillAwardFill />
                      </h1>
                      <div className="ribbon">{data.name}</div>
                    </div>
                  </a>
                ))}
              </div>

          </div>
        </section>
      </>
      :
      <div className="container"><div className="row min-vh-100 ">
        <div className="col my-auto"> 
        <img height="70px" src={Loader} alt="loader"/>
        </div>
        </div>
      </div>
      }
      </>
    </div>
  );
}

export default App;
