import axios from "axios";
import {useQuery} from "react-query";



export const useConf = () => {
    const query = useQuery('keyConfig', () => axios.get(`https://dev.msvstudio.co.id/auth/conf.json`).then(res => {
        return res.data;
    }))
    return {
        ...query,
        data: query.data
    }
}


export const useSmartMenu = (url, username) => {
    const urlMenu=url?.["menu-base"]+username


    const query = useQuery('keySmartMenu', () => axios.get(`${urlMenu}`).then(res => {
           return res.data;
    }, { refetchInterval: 1000 }))
    return {
        ...query,
        data: query.data
    }
}

export const useGetUsername =  (url, dataCookie) => {
    const urlProfile=url?.["profile-base"]+dataCookie
  
   
       const query = useQuery('keyName', () => axios.get(`${urlProfile}`).then(res => {
           return res.data[0]
    },{ refetchInterval:1000}))
    return {
        ...query,
        data: query.data
    }
}